.weather {
    place-self: center;
    padding:40px;
    border-radius: 10px;
    background-image: linear-gradient(45deg, #254680,#500ae4);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search-bar{
    display: flex;  
    align-items: center;
    gap: 12px;
}

.search-bar input {
    height: 50px;  
    border:none;
    outline : none;
    border-radius: 40px;
    padding-left: 25px;
    color : #626262;
    background: #ebfffc;
    font-size: 18px;
}

.search-bar img {
    width : 45px;  
    padding : 15px;
    border-radius: 50%;
    background: #ebfffc;
    cursor: pointer;
}

.weather-icon {
    width : 130px;
    margin : 10px 0;
}

.temperature {
    color : #fff;
    font-size : 80px;
    line-height: 1;
}

.location {
    color : #fff;
    font-size : 40px;
}

.weather-data {
    width : 100%;
    margin-top: 40px;
    color :#fff;
    display : flex;
    justify-content: space-between;
}

.weather-data .col {
    display : flex;
    align-items : flex-start;
    gap : 12px;
    font-size: 22px;
}

.weather-data .col span {
    display : block;
    font-size: 16px;
}

.weather-data .col img {
    width : 16px;
    margin-top: 10px;
}
